.header {
  width: 100%;
  height: 110px;
  position: fixed;
  top: 0;
  transition: all 0.3s;
  z-index: 99;
  background-color: var(--white-color);
}


.main_header {
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  padding: 15px 15px 0;
}

@media only screen and (max-width: 1000px) {
  .main_header {
    padding: 10px 0 0;
  }
}
.hide-navlinks {
  right: -100%;
}
.sidedrawer-navlinks {
  display: none;
}
@media only screen and (max-width: 767px) {
  .sidedrawer-navlinks {
    display: block;
  }
  .sidedrawer-navlink_wrapper {
    text-align: center;
    /* transition: all 0.2s; */
  }
  .sidedrawer-navlink_wrapper:hover {
    background-color: var(--secondary-color);
    color: var(--white-color);
  }
  .sidedrawer-navlink_wrapper:hover a {
    color: var(--white-color);
  }
  .sidedrawer-navlinks .nav-item {
    font-weight: 700;
    width: 100%;
    font-size: 1rem;
    line-height: 120%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px 0;
    color: var(--secondary-color);
    display: block;
  }

}
